import { useRef } from "../deps.ts";
export interface Props {
  label: string;
  menus: Option[];
  className?: string;
  maxWidth?: number;
  showArrow?: boolean;
  onSelected: (item: Option) => void;
}
export interface Option {
  label: string;
  value: string;
}
const DEFAULT_VALUE = "DROP_DOWN_DEFAULT_VALUE";
export default function SelectDropDown(props: Props) {
  let { showArrow, onSelected, className, menus, maxWidth } = props;
  className = className || "";
  showArrow = showArrow ?? true;
  maxWidth = maxWidth || 60;
  const ref = useRef<HTMLSelectElement>(null);
  return (
    <select
      ref={ref}
      autoComplete="off"
      class={`min-select ${showArrow ? "" : "min-select-no-arrow"} ${
        className ? className : ""
      }`}
      value={DEFAULT_VALUE}
      style={{ maxWidth: `${maxWidth}px` }}
      onChange={(e) => {
        e.preventDefault();
        const value = (e.target as HTMLInputElement).value;
        if (ref.current && value !== DEFAULT_VALUE) {
          ref.current.value = DEFAULT_VALUE;
          ref.current?.dispatchEvent(new Event("change"));

          const item = menus.find((item) => item.value === value);
          if (item) {
            onSelected(item);
          }
        }
      }}
    >
      {[{
        value: DEFAULT_VALUE,
        label: props.label,
      }].concat(menus).map((item) => (
        <option value={item.value}>
          {item.label}
        </option>
      ))}
    </select>
  );
}
